@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
:root {
	--transition: all 0.3s linear;
	--light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
	--dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
	--nav-color: #262627;
	--nav-text-color: #fefffe;
	--main-color-light: #191919;
	--main-color-dark: #111;
	--main-color-text: rgba(255, 255, 255, 0.8);
	--highlight-color: #1171e8;
	--hover-color: #e39d25;
	--bar-main-color: #01d5fe;
	--bar-compare-color: #fec260;
	--bar-final-color: #32fec3;
	--max-width: 900px;
	--spacing: 0.1rem;
	--radius: 0.25rem;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	word-wrap: break-word;
}
body {
	font-family: "Lato", sans-serif;
	background: #191919;
	background: var(--main-color-light);
	line-height: 1.5;
}

/* Navbar */
.nav-center {
	display: flex;
	background-color: var(--nav-color);
	color: var(--nav-text-color);
	box-shadow: var(--light-shadow);
	align-items: center;
	justify-content: space-between;
	padding: 2rem;
}
.nav-title {
	color: var(--main-color-text);
}
.range-input {
	display: flex;
	align-items: center;
	justify-content: center;
	grid-gap: 2rem;
	gap: 2rem;
}
.nav-count {
	display: flex;
	flex-direction: column;
	justify-content: center;
	grid-gap: 0.5rem;
	gap: 0.5rem;
}
.nav-delay {
	display: flex;
	flex-direction: column;
	justify-content: center;
	grid-gap: 0.5rem;
	gap: 0.5rem;
}
label {
	color: var(--main-color-text);
}
.count-input {
	cursor: pointer;
	height: 4px;
	width: 8rem;
}
.delay-input {
	cursor: pointer;
	height: 4px;
	width: 8rem;
}
select {
	padding: 0rem 0.5rem;
	color: var(--highlight-color);
	font-size: 1rem;
	background-color: transparent;
	cursor: pointer;
	letter-spacing: 0.05rem;
	border: none;
	transform: translateX(-0.8rem);
}
select:focus-visible {
	outline: none;
}
select option {
	background: var(--main-color-light);
}
.algo {
	display: flex;
	flex-direction: column;
	justify-content: center;
	grid-gap: 0.5rem;
	gap: 0.5rem;
}
.btn {
	background-color: transparent;
	color: var(--highlight-color);
	border: 1px solid var(--highlight-color);
	border-radius: var(--radius);
	cursor: pointer;
	font-size: 1rem;
	letter-spacing: 0.1rem;
	padding: 0.9rem 1rem;
	margin-right: 1rem;
	transition: var(--transition);
}
.btn:hover {
	color: var(--hover-color);
	border-color: var(--hover-color);
}
/* Bar */
.bar-container {
	display: flex;
	justify-content: center;
	grid-column-gap: 0.1rem;
	column-gap: 0.1rem;
	margin: 0 5rem;
}

